<template>
  <div class="employee-container">
    <el-card shadow="never">
      <div slot="header" class="clearfix">
        <span>销售订单管理</span>
        <el-button type="primary" size="small" @click="handleAdd" style="float: right; margin-right: 10px">新增订单</el-button>
      </div>
      <!-- 查询条件 -->
      <el-form :inline="true" :model="queryForm" class="query-form">
        <el-form-item label="员工编码">
          <el-input v-model="queryForm.employeeCode" placeholder="请输入员工编码" style="width: 150px;"></el-input>
        </el-form-item>
        <el-form-item label="员工姓名">
          <el-input v-model="queryForm.employeeName" placeholder="请输入员工姓名" style="width: 150px;"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="queryForm.status" placeholder="请选择状态" style="width: 120px;">
            <el-option label="全部" :value="''"></el-option>
            <el-option label="启用" :value="1"></el-option>
            <el-option label="禁用" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleQuery" icon="el-icon-search">查询</el-button>
          <el-button @click="handleReset" icon="el-icon-refresh">重置</el-button>
        </el-form-item>
      </el-form>

      <!-- 数据表格 -->
      <el-table
        v-loading="loading"
        :data="pageData"
        @selection-change="handleSelectionChange"
        style="width: 100%"
      >
        <el-table-column prop="employeeCode" label="员工编码"></el-table-column>
        <el-table-column prop="employeeName" label="员工姓名"></el-table-column>
        <el-table-column prop="phone" label="手机号"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status === 1" type="success">启用</el-tag>
            <el-tag v-else type="danger">禁用</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150" fixed="right">
          <template slot-scope="scope">
            <el-button @click="handleEdit(scope.row)" type="primary" size="small">编辑</el-button>
            <el-button @click="handleDelete(scope.row.id)" type="danger" size="small" >删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页控件 -->
      <div class="pagination">
        <el-pagination
          background
          layout="prev, pager, next, jumper, ->, total"
          :total="total"
          :current-page="queryForm.pageNum"
          :page-size="queryForm.pageSize"
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
        ></el-pagination>
      </div>
    </el-card>

    <!-- 员工表单对话框 -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="700px">
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="员工编码" prop="employeeCode">
              <el-input v-model="form.employeeCode" :disabled="!isAdd" placeholder="自动生成"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="员工姓名" prop="employeeName">
              <el-input v-model="form.employeeName" placeholder="请输入员工姓名"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="手机号" prop="phone">
              <el-input v-model="form.phone" placeholder="请输入手机号"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="状态" prop="status">
              <el-switch v-model="form.status" :active-value="1" :inactive-value="0"></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="handleSubmit">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getToken, getCompanyId } from '@/utils/auth'
import {employeeAPI} from "@/api/api";

export default {
  name: 'Employee',
  data() {
    return {
      loading: false,
      pageData: [],
      total: 0,
      selectedIds: [],
      dialogVisible: false,
      isAdd: true,
      dialogTitle: '新增员工',
      queryForm: {
        pageNum: 1,
        pageSize: 10,
        employeeCode: '',
        employeeName: '',
        department: '',
        status: ''
      },
      form: {
        id: '',
        companyId: '',
        employeeCode: '',
        employeeName: '',
        phone: '',
        status: 1
      },
      rules: {
        employeeName: [
          { required: true, message: '请输入员工姓名', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '请输入手机号', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.loadData()
  },
  methods: {
    // 加载数据
    loadData() {
      this.loading = true
      this.queryForm.companyId = getCompanyId()
      employeeAPI.page(this.queryForm).then(res => {
        this.loading = false
        if (res.code === 200) {
          this.pageData = res.data.records
          this.total = res.data.total
        } else {
          this.$message.error(res.msg)
        }
      }).catch(() => {
        this.loading = false
      })
    },

    // 查询
    handleQuery() {
      this.queryForm.pageNum = 1
      this.loadData()
    },

    // 重置
    handleReset() {
      this.queryForm = {
        pageNum: 1,
        pageSize: 10,
        employeeCode: '',
        employeeName: '',
        status: ''
      }
      this.loadData()
    },

    // 分页变化
    handleCurrentChange(val) {
      this.queryForm.pageNum = val
      this.loadData()
    },

    // 每页条数变化
    handleSizeChange(val) {
      this.queryForm.pageSize = val
      this.loadData()
    },

    // 选择变化
    handleSelectionChange(val) {
      this.selectedIds = val.map(item => item.id)
    },

    // 新增
    handleAdd() {
      this.isAdd = true
      this.dialogTitle = '新增员工'
      this.resetForm()
      this.form.companyId = getCompanyId()
      // 生成员工编码
      employeeAPI.generateCode({ companyId: 1 }).then(res => {
        if (res.code === 200) {
          this.form.employeeCode = res.data
        }
      })
      this.dialogVisible = true
    },

    // 编辑
    handleEdit(row) {
      this.isAdd = false
      this.dialogTitle = '编辑员工'
      this.resetForm()
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
    },

    // 重置表单
    resetForm() {
      this.form = {
        id: '',
        companyId: '',
        employeeCode: '',
        employeeName: '',
        phone: '',
        status: 1
      }
      if (this.$refs.form) {
        this.$refs.form.resetFields()
      }
    },

    // 提交
    handleSubmit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const params = {
            ...this.form,
            status: Number(this.form.status)
          }
          if (this.isAdd) {
            employeeAPI.add(params).then(res => {
              if (res.code === 200) {
                this.$message.success('新增成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.msg)
              }
            })
          } else {
            employeeAPI.update(params).then(res => {
              if (res.code === 200) {
                this.$message.success('修改成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.msg)
              }
            })
          }
        }
      })
    },

    // 删除
    handleDelete(id) {
      this.$confirm('确定要删除这条数据吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        employeeAPI.delete(id).then(res => {
          if (res.code === 200) {
            this.$message.success('删除成功')
            this.loadData()
          } else {
            this.$message.error(res.msg)
          }
        })
      })
    },

    // 批量删除
    handleBatchDelete() {
      if (this.selectedIds.length === 0) {
        this.$message.warning('请选择要删除的数据')
        return
      }
      this.$confirm('确定要删除选中的数据吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        employeeAPI.deleteBatch(this.selectedIds).then(res => {
          if (res.code === 200) {
            this.$message.success('批量删除成功')
            this.selectedIds = []
            this.loadData()
          } else {
            this.$message.error(res.msg)
          }
        })
      })
    },

    // 批量启用
    handleEnableBatch() {
      if (this.selectedIds.length === 0) {
        this.$message.warning('请选择要启用的数据')
        return
      }
      employeeAPI.enableBatch(this.selectedIds).then(res => {
        if (res.code === 200) {
          this.$message.success('批量启用成功')
          this.selectedIds = []
          this.loadData()
        } else {
          this.$message.error(res.msg)
        }
      })
    },

    // 批量禁用
    handleDisableBatch() {
      if (this.selectedIds.length === 0) {
        this.$message.warning('请选择要禁用的数据')
        return
      }
      employeeAPI.disableBatch(this.selectedIds).then(res => {
        if (res.code === 200) {
          this.$message.success('批量禁用成功')
          this.selectedIds = []
          this.loadData()
        } else {
          this.$message.error(res.msg)
        }
      })
    },

    // 检查员工名称是否重复
    checkName() {
      if (!this.form.employeeName) return
      employeeAPI.checkName({
        employeeName: this.form.employeeName,
        companyId: getCompanyId(),
        id: this.isAdd ? null : this.form.id
      }).then(res => {
        if (res.code === 200 && !res.data) {
          this.$message.error('员工名称已存在')
          this.form.employeeName = ''
        }
      })
    },

    // 检查身份证号是否重复
    checkIdCard() {
      if (!this.form.idCard) return
      employeeAPI.checkIdCard({
        idCard: this.form.idCard,
        companyId: getCompanyId(),
        id: this.isAdd ? null : this.form.id
      }).then(res => {
        if (res.code === 200 && !res.data) {
          this.$message.error('身份证号已存在')
          this.form.idCard = ''
        }
      })
    },

    // 检查手机号是否重复
    checkPhone() {
      if (!this.form.phone) return
      employeeAPI.checkPhone({
        phone: this.form.phone,
        companyId: getCompanyId(),
        id: this.isAdd ? null : this.form.id
      }).then(res => {
        if (res.code === 200 && !res.data) {
          this.$message.error('手机号已存在')
          this.form.phone = ''
        }
      })
    }
  }
}
</script>

<style scoped>
.employee-container {
  padding: 20px;
}

.query-form {
  margin-bottom: 20px;
}

.action-buttons {
  margin-bottom: 20px;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}
</style>
