// 认证相关工具函数

// 存储token
export function setToken(token) {
  return localStorage.setItem('token', token)
}

// 获取token
export function getToken() {
  return localStorage.getItem('token')
}

// 清除token
export function removeToken() {
  return localStorage.removeItem('token')
}

// 存储用户信息
export function setUserInfo(userInfo) {
  return localStorage.setItem('userInfo', JSON.stringify(userInfo))
}

// 获取用户信息
export function getUserInfo() {
  const userInfo = localStorage.getItem('userInfo')
  return userInfo ? JSON.parse(userInfo) : null
}

// 清除用户信息
export function removeUserInfo() {
  return localStorage.removeItem('userInfo')
}

// 存储公司ID
export function setCompanyId(companyId) {
  return localStorage.setItem('companyId', companyId)
}

// 获取公司ID
export function getCompanyId() {
  return localStorage.getItem('companyId')
}

// 清除公司ID
export function removeCompanyId() {
  return localStorage.removeItem('companyId')
}

// 登出
export function logout() {
  removeToken()
  removeUserInfo()
  removeCompanyId()
}

// 判断是否已登录
export function isLoggedIn() {
  return !!getToken()
}
